.welcome-section{
    background-color: $clr-primaryBg;
    color: white;
    .first-svg{
        position: absolute;
        top: 33%;
        left: -100px;
    }
    .welcome-main-container{
        padding-block-start: 130px;
        height: 87vh;
        .welcome-title{
            font-size: 5rem;
            flex-basis: 60%;
            font-family: 'semi-bold';
            span{
                color: $clr-light-coral;
            }
        }
        .welcome-p{
            width: 445px;
            font-family: 'regular';
            font-size: 1.5rem;
            &::before{
                content: '';
                display: inline-block;
                top: 0;
                width: 50px;
                height: 4px;
                background-color: $clr-light-blue;
            }
            p{
                margin-block-start: 3rem;
            }
        }
    }
    .second-svg{
        position: absolute;
        bottom: 0;
        right: 165px;
    }
}