.app-header {
    padding-block-start: 73px;
    background-color: $clr-primaryBg;
    font-family: 'regular';

    .main-header {
        color: white;

        .main-header-left {
            width: 376px;

            img {
                width: 160px;
            }
        }

        .main-header-right {
            svg {
                display: none;
            }
        }
    }

    .opened-nav {
        position: fixed;
        top: 0;
        right: 0;
        width: 70%;
        height: 100%;
        background-color: $clr-dark-blue;

        &.hide {
            display: none;
        }

        ul {
            list-style: none;

            li {
                padding: 1rem;
                margin-inline-start: 1.5rem;
            }
        }

        .close-btn {
            float: right;
            background: transparent;
            border: 0;
            color: white;
            font-size: 1.5rem;
            cursor: pointer;
        }
    }
    .contact-us-btn {
        font-family: 'regular';
        font-size: 18px;
        width: 153px;
        height: 48px;
        background: transparent;
        border: 2px solid white;
        border-radius: 30px;
        color: white;
    }
}