.features-section{
    background-color: $clr-second-bg;
    color: white;
    overflow-x: hidden;
    .features-main-container{
        height: 100vh;
        .inner-container{
            width: 100%;
            .features-l{
                flex-basis: 70%;
                .features-title{
                    &::before{
                        content: '';
                        display: inline-block;
                        top: 0;
                        width: 50px;
                        height: 4px;
                        background-color: $clr-light-coral;
                    }
                    h1{
                        margin-block-start: 3rem;
                        font-size: 3rem;
                        font-family: 'semi-bold';
                    }
                }
            }
            .features-r{
                margin-block-start: 5rem;
                .features-card{
                    margin-block-end: 2rem;
                    font-family: 'regular';
                    .features-card-text{
                        margin-inline-start: 1rem;
                        h5{
                            font-size: 1.2rem;
                            margin-block-end: 0.3rem;
                            color: $clr-light-coral;
                        }
                        p{
                            padding-block-end: 0.2rem;
                        }
                    }
                }
            }
        }
    }
    .features-svg{
        position: relative;
        left: 265px;
        top: 335px;
    }
}