@media (max-width: 450px) {
    body {
        svg {
            display: none;
        }
    }

    .main-layout {
        padding: 0 5%;
    }

    .app-header {
        padding-block-start: 30px;
        .main-header {
            .main-header-left {
                span {
                    display: none;
                }
            }

            .main-header-right button {
                display: none;
            }

            .main-header-right svg {
                display: inline;
                cursor: pointer;
            }
        }
    }


    .welcome-section {
        .welcome-main-container {
            flex-direction: column;

            .welcome-title {
                flex-basis: 50%;

                h1 {
                    font-size: 2rem;
                    text-align: center;
                }
            }

            .welcome-p {
                max-width: 100%;

                p {
                    font-size: 1rem;

                }
            }
        }
    }

    .features-section {
        .features-main-container {
            .inner-container {
                flex-direction: column;

                .features-l {
                    .features-title {
                        h1 {
                            font-size: 2.5rem;
                        }
                    }
                }
            }
        }
    }

    .reviews-section {
        .reviews-main-container {
            padding-block-start: 1rem;

            h1 {
                font-size: 2rem;
            }

            .reviews-cards {
                flex-direction: column;

                .review-card {
                    width: 100%;
                    margin: 1rem 0;

                    .review-card-container {
                        p {
                            font-size: 1rem;
                        }
                    }
                }
            }
        }
    }
}