.reviews-section{
    height: 100vh;
    background-color: $clr-deep-green;
    .reviews-main-container{
        padding-block-start: 5rem;
        h1{
            color: white;
            font-size: 4rem;
            text-align: center;
            font-family: 'semi-bold';
            span{
                color: $clr-light-blue;
            }
        }
        .reviews-cards{
            margin-block-start: 2rem;
            height: 313px;
            .review-card{
                width: 30%;
                .review-card-container{
                    width: 85%;
                    height: 100%;
                    font-family: 'light';
                    color:white;
                    & > p{
                        z-index: 2;
                        font-size: 1.2rem;
                    }
                    svg{
                        position: absolute;
                        margin-block-end: 15rem;
                        margin-inline-start: 8rem;
                    }
                    .review-user{
                        text-align: center;
                        h3{
                            color: $clr-light-blue;
                        }
                    }
                    img{
                        width: 62px;
                        border-radius: 50%;
                        border: 2px solid #C4FFFE;
                        margin: 0 auto;
                    }
                }
            }
        }
    }
    .bottom-review-svg{
        position: relative;
        left: 89%;
        top: 131px;
    }
}